.App {
  max-width: 860px;
  margin: 10px auto;
}
.game-settings {
  margin: 0 auto;
  width: fit-content;
  padding: 1rem;
  border: 2px solid;
  border-radius: 1rem;
  background-color: #2c1e3e;
}
button {
  background: none;
  border: 2px solid #fff;
  padding: 6px 12px;
  border-radius: 4px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  font-size: 1em;
  box-shadow: 0 5px 5px rgba(0, 0, 0, 0.1);
}
button:hover {
  background: #c23866;
  color: #fff;
}
.card-grid {
  margin: 40px 20px 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 2rem;
}

.card-grid-medium {
  margin: 40px 20px 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-gap: 2rem;
}

.card-grid-hard {
  margin: 40px 20px 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-gap: 2rem;
}

.select-difficulty {
  margin: 20px 0 0;
  font-family: Raleway, sans-serif;
  padding: 5px;
  font-size: 1.5rem;
  background-color: #2c1e3e;
  color: white;
  border-radius: 10px;
  outline: none;
}

.turns {
  background-color: #fff;
  padding: .25rem 1rem;
  border: 3px solid #c23866;
  
  
  border-bottom-left-radius: 16px;
  border-top-left-radius: 16px;
  color: black;
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: 2.5rem;
  position: relative;
  float: right;
  top: 0;
  right: 0;
}

.turns img {
  width: 40px;
  margin-right: 5px;
}

@media screen and (max-width: 600px){
  .card-grid {
    margin: 20px 0;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: .5rem;
  }

  .card-grid-medium {
    margin: 20px 0;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: .5rem;
  }
  
  .card-grid-hard {
    margin: 20px 0;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: .5rem;
  }

  .turns {
    opacity: 70%;
    position: fixed;
    top: 100px;
    border-right: 0;
  }

}