.card {
    position: relative;
}
.card img {
    width: 100%;
    display: block;
    border: 2px solid #fff;
    border-radius: 6px;
}

/* front of card - the picture */
.card .front {
    background-color: #2c1e3e;
    transform: rotateY(90deg);
    transition: all ease-in .2s;
    position: absolute;
}
.flipped .front {
    transform: rotateY(0deg);
    transition-delay: .2s;
}

/* back of card - cover */
.card .back {
    transition: all ease-in .2s;
    transition-delay: .2s;
    cursor: pointer;
}
.flipped .back {
    transform: rotateY(90deg);
    transition-delay: 0s;
}

@media screen and (max-width: 600px){
    .card img{
        border: 1px solid #fff;
    }
}
